.Icon-information {
  text-decoration: none;
}

.Title {
  font-size: 400;
}

.Greeting {
  text-align: left;
  padding-left: 2em;
}

.App {
  display: flex;
  justify-content: space-between;
  text-align: center;
}

span {
  border: none;
  border-radius: 1em;
  font-size: 1em;
  display: block;
  text-align: center;
}

.total {
  margin-top: 2em;
}

.Footer-inner {
  margin-top: 2em;
  margin-bottom: 2em;
  margin-right: 3em;
  margin-left: 3em;
  display: flex;
  justify-content: space-between;
}

.Icon-information {
  display: flex;
  flex-direction: column;
  align-items: center;
}

h1,
h2,
h3 {
  color: #4e342e;
  font-style: normal;
  font-weight: 300;
}

.Buttons-block {
  margin: 0 auto;
  max-width: 1200px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.Buttons-container {
  display: flex;
  flex-direction: column;
  margin: 3em auto;
}

.Inner-data {
  display: flex;
  justify-content: space-between;
}
